
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import Search from 'vux/src/components/search/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import serviceGetData from '../../service/getData.js'
import dateFormat from 'vux/src/tools/date/format.js'

import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import XDialog from 'vux/src/components/x-dialog/index.vue'

import service_upData from '../../service/upData.js'

export default {
  directives: {
      TransferDom
  },
  components: {
    Search, Scroller, Divider, Spinner,
    XHeader, Checker, CheckerItem, Confirm, XDialog, Toast
  },
  created(){

    // 
      // page | int | 
      // length | int | 
      // createTimeStart | Long | 
      // createTimeEnd | Long | 
      // status | int | 1 2
      // type | int | 1  2  3
      // filter | String | 
      // sort | int | 1 2


    serviceGetData.info_list({
      createTimeEnd:(+new Date()),
      createTimeStart:(+new Date())-{1:2592000000, 2:7776000000, 3:31536000000}[this.user_time], // 
      type:(this.user_type==4 ? '' : this.user_type), //  
      status:this.user_status, // 1 2
      filter:this.filter, // 
      page:this.user_page, // 
      sort:this.user_sort, // 1 2
      length:this.user_length, // 

    }).then(res=>{
      this.info_list = res
      this.$store.state.info_list = res
    },res=>console.log(res))


  },
  filters:{
    dateFormat(par,form){
      return dateFormat(new Date(par), form)
    }
  },
  data () {
    return {
      n: 10,
      pullupEnabled: true,
      status: {
        pullupStatus: 'default',
        pulldownStatus: 'default'
      },
      pulldown_config: {
        content: 'content',
        pullUpHeight: 60,
        height: 40,
        autoRefresh: false,
        downContent: '',
        upContent: '',
        loadingContent: '...',
        clsPrefix: 'clsPrefix'
      },
      no_moer:false, // 
      value: '',

      user_time:1, //  
      user_type:1, // 1  2  3
      user_status:1, // 1 2
      user_filter:'', // 
      user_page:1, // 
      user_sort:1, // 1 2
      user_length:10, // 

      // user_page_load:1, // 
      filter_status:null,
      filter_type:null,
      toast_load:false,
      results: [], // 
      data_index:-1,// 
      info_list:{}, // 
      confirm_show:false, // 
      showContent:false, // 
      cancel_pre_id:'', // 
    }
  },
  computed:{
    to_user_time(){
      var mydate = new Date();
      return {one_month:mydate-2592000000, three_months:mydate-7776000000, a_year:mydate-31536000000}
    }
  },
  methods:{
    loadMore () {
      console.log(this.info_list.data.length , this.info_list.total)
      if( this.info_list.data.length >= this.info_list.total ){
        // alert('')
        console.log('')
        this.$refs.scroller.disablePullup()
        this.$refs.scroller.donePulldown()
        this.no_moer=true

        // 

      }else{
        this.no_moer=false

        // 

          setTimeout(() => {
            this.user_page ++
            setTimeout(() => {

              serviceGetData.info_list({
                createTimeEnd:(+new Date()),
                createTimeStart:(+new Date())-{1:2592000000, 2:7776000000, 3:31536000000}[this.user_time], // 
                type:(this.user_type==4 ? '' : this.user_type), //  
                status:this.user_status, // 1 2
                filter:this.filter, // 
                page:this.user_page, // 
                sort:this.user_sort, // 1 2
                length:this.user_length, // 

              }).then(res=>{
                this.info_list.data = this.info_list.data.concat(res.data)
                this.$store.state.info_list.data = this.$store.state.info_list.data.concat(res.data)

                this.$refs.scroller.donePullup()

              },res=>console.log(res))

            }, 10)
          }, 300) // 

      }

      // 
        // setTimeout(() => {
        //   this.n += 10
        //   setTimeout(() => {
        //     this.$refs.scroller.donePullup()
        //   }, 10)
        // }, 2000)
    },

    refresh () {
      // 
        this.$nextTick(() => {
          this.no_moer=false
          // 
          this.$refs.scroller.donePulldown()
          this.$refs.scroller.enablePullup()
          this.pullupEnabled = true
        })


      // 
        // setTimeout(() => {
        //   this.n = 10
        //   this.$nextTick(() => {
        //     setTimeout(() => {
        //       this.$refs.scroller.donePulldown()
        //       this.pullupEnabled && this.$refs.scroller.enablePullup()
        //     }, 10)
        //   })
        // }, 2000)
    },
    changePullupStatus (enabled) {
      if (enabled) {
        this.$refs.scroller.enablePullup()
        this.pullupEnabled = true
      } else {
        this.$refs.scroller.disablePullup()
        this.pullupEnabled = false
      }
    },
    cancel_pre_ok(){
      service_upData.cancel_pre(this.cancel_pre_id).then(res=>{
        this.toast_text=''
        this.toast_load=true
        this.info_list.data.splice(this.data_index,1); // 
      },res=>{
        this.toast_text=''
        this.toast_load=true
      })
    },
    click_item(index, aid_aiId, text, type){
      this.$router.push({name:'details',query:{index:index, aid_aiId:aid_aiId, text:text, type:type}})
      console.log(index, aid_aiId, type)
    },
    click_pre(index,id){
      this.data_index = index // 
      this.cancel_pre_id=id
      this.confirm_show=true
    },

    resultClick(item){
      console.log(item)

      serviceGetData.info_list({
        createTimeEnd:(+new Date()),
        createTimeStart:(new Date() - 31536000000), // ,
        // status:this.user_status,
        // type:this.user_type == 4 ? '' : this.user_type ,
        sort:this.user_sort,
        filter:item.myval,
      }).then(res=>{
          this.info_list = res
          this.$store.state.info_list = res
      },res=>console.log(res))

      // this.info_list.data=[]
      // alert( JSON.stringify(item) )
    },
    getResult (val) {
      this.results = val ? getResult(this.value) : []
    },
    switch_sort(){

      var createTimeEnd=(+new Date());
      var createTimeStart=(new Date() - 31536000000); // 

      this.user_sort = this.user_sort==1 ? 2 : 1;

      serviceGetData.info_list({
        createTimeEnd:(+new Date()),
        createTimeStart:(+new Date())-{1:2592000000, 2:7776000000, 3:31536000000}[this.user_time], // 
        type:(this.user_type==4 ? '' : this.user_type), //  
        status:this.user_status, // 1 2
        filter:this.filter, // 
        page:this.user_page, // 
        sort:this.user_sort, // 1 2
        length:this.user_length, // 

      }).then(res=>{
        this.info_list = res
        this.$store.state.info_list = res
      },res=>console.log(res))


    },
    reset(){
      this.user_time=1; //  
      this.user_type=1; //  
      this.user_status=1; // 1 2
      this.user_filter=''; // 
      this.user_page=1; // 
      this.user_sort=1; // 1 2
      this.user_length=10; // 
    },
    confirm(){
      if(this.user_time==''){
        alert('')
        return
      }
      this.showContent=!this.showContent

      // 
        this.user_page=1
        this.$refs.scroller.donePulldown()
        this.$refs.scroller.enablePullup()
        this.pullupEnabled = true

      serviceGetData.info_list({
        createTimeEnd:(+new Date()),
        createTimeStart:(+new Date())-{1:2592000000, 2:7776000000, 3:31536000000}[this.user_time], // 
        type:(this.user_type==4 ? '' : this.user_type), //  
        status:this.user_status, // 1 2
        filter:this.filter, // 
        page:this.user_page, // 
        sort:this.user_sort, // 1 2
        length:this.user_length, // 

      }).then(res=>{
        this.info_list = res
        this.$store.state.info_list = res
      },res=>console.log(res))


    },
  }
}



function getResult (val) {
  $('.weui-cells.vux-search_show').show()
  // .prepend(`
  //     <div class="getResult_xw"></div>

  //   `);
  let rs = [{
    title: ``,
    myval: val
  }]
  // for (let i = 0; i < 20; i++) {
  //   rs.push({
  //     title: `${val} result: ${i + 1} `,
  //     other: i
  //   })
  // }
  return rs
}










